.text {
    color: #FFF;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin-right: 8px;
    padding: 14px 0 14px 32px; }

.footer {
    min-height: 330px;
    background: #263238;
    &__container {
        display: grid;
        grid-template-columns: 2fr 1fr 1fr 1fr;
        padding-top: 65px; }
    &__logo {
        margin-bottom: 40px; }
    &__text1 {
        color: #F5F7FA;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 8px; }
    &__text2 {
        color: #F5F7FA;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        margin-bottom: 40px; }
    &__icons {
        display: inline-flex;
        /* align-items: center */
        /* justify-items: center */ }
    &__icon {
        background: #4F595E;
        margin-right: 16px;
        border: none;
        border-radius: 50%;
        padding: 8px;
        &:last-child {
            margin-right: 0; } }
    &__title {
        color: #FFF;
